@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');
h1, h2, h3, h4, h5, h6 {
  color: white;
  margin-left: auto;
  margin-right: auto;
  padding: 5px;
  margin-bottom: 5px;
  text-decoration: underline;
}
.text-blue {
  color: #002B7F;
}

.text-yellow {
  color: #FCD116;
}

.text-red {
  color: #CE1126;
}
h1 {
  font-size: 1.4rem;
  width: fit-content;
}
.prev-word-h3, .prev-word-h2 {
  width: fit-content;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  font-size: inherit;
  text-decoration: none;
  font-size: 0.8rem;
}
.prev-word-h2 {
  margin-top: -3px;
  font-size: 1.3rem;
  text-shadow: -1px 2px rgba(0, 43, 127, 0.3),
          -2px 2px rgba(252, 209, 22, 0.3),
          -3px 2px rgba(206, 17, 38, 0.3);
  font-style: italic;
}
body {
  font-family: "Bungee", Helvetica, sans-serif;
  background-color: rgb(18, 18, 19);
}
.game-row {
  display: flex;
  justify-content: center;
}
p, .react-icon {
  color: #ffffff;
  margin: 0;
  padding: 0;
  text-align: center;
}

.letter-box {
  width: 55px;
  height: 58px;
  font-size: 1.8rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 3px;
  border-radius: 10% 10% 10% 10%;
}
.empty-letter {
  background-color: rgb(58, 58, 60);
}
.guessed-letter {
  background-color: rgb(83, 141, 78);
}
.half-letter {
  background-color: rgb(181, 159, 59);
}

.letter-box {
  transition: transform 0.5s linear, background-color 0.5s ease-in-out, width 0.5s linear;
  transform-style: preserve-3d;
}

.letter-flip {
  transform: rotateX(360deg);
}

.letter {
  backface-visibility: hidden;
}

.game-state {
  font-family: cursive;
  font-size: 1.5rem;
  margin-top: 20px;
  font-weight: bold;
  font-style:italic;
}

.key-row {
  display: grid;
  justify-content: center;
  gap: 3px; 
  width: 100%;
  max-width: 500px;
  margin-bottom: 3px;
}

.key-row.row-1 {
  grid-template-columns: repeat(10, 1fr); 
}

.key-row.row-2 {
  grid-template-columns: 0.5fr repeat(9, 1fr) 0.5fr; 
}

.key-row.row-3 {
  grid-template-columns: 1.5fr repeat(7, 1fr) 1.5fr; 
}

.key-box {
  background-color: rgb(92, 92, 92);
  width: 100%;
  height: 58px;
  font-size: 1.4rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  border-radius: 10% 10% 10% 10%;
  user-select: none;
}
.key-box:active {
  background-color: rgb(122, 122, 122);
}
.key-box > p {
  user-select: none;
}
.longer {
  width: 40px;
}
.keyboard-grid {
  background-color: rgb(18, 18, 19);
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 500px;
  max-height: 249px;
  z-index: 999;
  margin-top: 10px;
  margin-bottom: 20px;
  padding: 0 12px; 
  box-sizing: border-box; 
}

.game-grid {
  padding-bottom: 249px;
}